.about-container{
    justify-content: center;
    text-align: center;
    align-items: center;
    align-content: center;
    flex-direction: column;
    margin: 0 6.5rem;
    margin-top: 3.7rem;
    flex-direction: column;
    /* animation: openAnimation 0.8s ease-in-out; */
    animation: openAnimation 0.8s ease-in-out, pageTransition 0.8s ease-in-out;
}

@keyframes openAnimation {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes pageTransition {
    from {
      opacity: 0;
      transform: translateY(50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

/* @keyframes openAnimation {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
} */

.abt-head{
    align-content: center;
    justify-content: center;
    flex-direction: column; 

        h1{
            text-decoration: underline;
            font-size: 3.5rem;
        }
    }

.abt-line{
    display: none;   
}

.about{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    position:sticky;
    overflow: hidden;
    background: linear-gradient(90deg, rgba(3, 6, 55, 0.7) 100%);
    border: 7px solid #030637;
    border-top-left-radius: 3rem;
    border-bottom-right-radius: 3rem;
    box-shadow: 0 0 2rem rgba(228, 135, 229, 0.5);
    animation: openAnimation 0.9s ease-in-out;
    
}
.abt-content{
    display: flex;
    justify-content: center;
    /* border: 7px solid #030637; */
    animation: openAnimation 1s ease-in;
}

.fade-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 45%;
    height: 20%; /* Adjust the height of the fade as needed */
    background: linear-gradient(to bottom, rgba(3, 6, 55, 0), rgba(3, 6, 55, 10));
/* Allow interactions with the underlying image */
    transform: translateY(-0.4rem);
    /* border: 3px solid red; */
}

.text-right{
    width: 38rem;
    height: 17rem;
    margin: 2rem;
    /* margin-top: 6rem; */
    /* transform: translateY(4rem); */
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    box-shadow: 0 0 2rem rgba(228, 135, 229, 0.1);
    /* transition: box-shadow 0.5s ease-in-out; */
    /* transition: background-color 0.6s ease-in-out; */
}

.text-right:hover{
    box-shadow: 0 0 2rem rgba(228, 135, 229, 0.5);
    background-color: #1f1e63;
    transition: background-color 0.4s ease-in-out;
}
.text{
    justify-content: center;
    text-align: center;
}
.text .my-about-name{
    color: #c191c2;
    text-shadow: #c191c2;
    line-height: 0.5rem;
    font-weight: 600;
    font-size: larger;
}

.box .div-box{
    position: absolute;
    width: 3.8rem;
    height: 3.8rem;
    border-radius: 50%;
    background-color: transparent;
    border: 6px solid rgba(255,255,255,0.8);
}

.box .div-box:nth-child(1){
    top: 12%;
    left: 42%;
    animation: animate 10s linear infinite;
}
.box .div-box:nth-child(2){
    top: 70%;
    left: 50%;
    animation: animate 7s linear infinite;
}
.box .div-box:nth-child(3){
    top: 17%;
    left: 6%;
    animation: animate 9s linear infinite;
}
.box .div-box:nth-child(4){
    top: 20%;
    left: 60%;
    animation: animate 10s linear infinite;
}
.box .div-box:nth-child(5){
    top: 67%;
    left: 10%;
    animation: animate 6s linear infinite;
}
.box .div-box:nth-child(6){
    top: 80%;
    left: 7%;
    animation: animate 12s linear infinite;
}
.box .div-box:nth-child(7){
    top: 60%;
    left: 80%;
    animation: animate 15s linear infinite;
}
.box .div-box:nth-child(8){
    top: 32%;
    left: 25%;
    animation: animate 16s linear infinite;
}
.box .div-box:nth-child(9){
    top: 91%;
    left: 25%;
    animation: animate 9s linear infinite;
}
.box .div-box:nth-child(10){
    top: 20%;
    left: 80%;
    animation: animate 5s linear infinite;
}

@keyframes animate{
    0%{
        transform: scale(0) translateY(0) rotate(0);
        opacity: 1;
    }
    100%{
        transform: scale(1.3) translateY(-90px) rotate(360deg);
        opacity: 0;
    }
}

/* ipads */
@media only screen and (max-width: 1050px) {
    .about-container{ 
        margin: 1.4rem;
      }
      .abt-content{
        /* border: yellow 2px solid; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        padding-top: -5rem;
    }
    .fade-bottom{
        
        margin-left: 190px;
        transform: translateY(-309px);
    }
     
}
/* Mobile */
@media only screen and (max-width: 700px) {
    
    .about{
        height: 42.6rem;
    }
    .about-container{ 
      margin: 1.8rem;
      
    }
    .abt-head{
        align-content: center;
        justify-content: center;
        flex-direction: column; 
        
            h1{
                text-decoration: underline;
                font-size: 3.5rem;
                
            }
        }
    .abt-content{
        /* border: yellow 2px solid; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        padding-top: -5rem;

    }
    .pic-left{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        text-align: center;
        opacity: 0.5;
        transform: translateY(-3.9rem);

    }
    .pic-left .fade-bottom{
        display: none;
    }
    .abt-head{
        align-content: center;
        justify-content: center;
        flex-direction: column; 
            h1{
                text-decoration: underline;
                font-size: 1.5rem;
            }
        }
        .text-right{
            width: 16rem;
            height: 27rem;
            transform: translateY(-20rem);
            border-top-left-radius: 2rem;
            border-bottom-right-radius: 2rem;
            background-color: rgba(3, 6, 55, 0.7);
            box-shadow: 0 0 2rem rgba(228, 135, 229, 0.1);
            transition: box-shadow 0.5s ease-in-out;
        }

        .text-right:hover{
            box-shadow: 0 0 2rem rgba(228, 135, 229, 0.5);
            
            background-color: #1f1e63;
            transition: background-color 0.4s ease-in-out;
            transition: background-color 0.4s ease-out;
        }
}